import "./styles.css";
import { useState, useMemo, useEffect } from "react";
export default function App() {
  const [c, sc] = useState(0);
  const [i, si] = useState("");
  const memo = useMemo(() => {
    return expop(i);
  }, [i]);

  const hc = () => {
    sc(c + 1);
  };

  useEffect(() => {
    console.log("counter");
  }, [c]);
  return (
    <div className="App">
      <input type="text" value={i} onChange={(e) => si(e.target.value)} />
      <button onClick={() => hc()}>Inc</button>
      <h1>
        {c}+'-'+
        {i}+'-'+
        {memo}
      </h1>
    </div>
  );
}

const expop = (v) => {
  for (let i = 0; i < 10000; i++) {}
  return v;
};
